import news from '../../api/news'
import * as types from '../mutation-types'

const state = {
  news: []
}

const actions = {
  getNewsBaseApi ({commit}) {
    console.log('action')
    news.newsBaseMsgApi()
    .then(res => {
      console.log('actionaxios')
      commit(types.GET_NEWS_API, res)
      console.log(res)
    })
  }
}

const getters = {
  allNews: state => state.news
}

const mutations = {
  [types.GET_NEWS_API] (state, res) {
    console.log('mutation')
    state.news = news
    console.log('mutationsstate')
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



