<template lang="html">
  <div class="message_list">
    <!-- <mt-cell-swipe v-for="item in messages" :key="item" class="list_item" :right="[{
      content: '',
      style: { background: '#ff3b2f', color: '#fff', width: '4rem',textAlign:'center', lineHeight:'5.3rem',fontSize:'1.3rem'},
      handler: () => this.$messagebox('delete')}]"> -->
      <div v-for="item in messages" :key="item" class="list_item">
        <img :src="item.avatar" alt="" />
        <div class="middle">
          <h3>{{item.id}}</h3>
          <p>{{item.recentMessage}}</p>
        </div>
        <div class="right">
          <div class="date">{{item.date}}</div>
        </div>
      </div>
    <!-- </mt-cell-swipe> -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
    }
  },
  computed: mapGetters({
    messages: 'allMessages'
  }),
  created () {
    this.$store.dispatch('getMessagesBaseApi')
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/css/reset.css';
.message_list {
  height: 100rem;
  background-color: #fff;
  & .list_item {
    height: 5.3rem;
    border-bottom: 1px solid #eaeaea;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    & img {
      width: 3.5rem;
      height: 3.5rem;
      margin-top: .85rem;
      padding: 0 .8rem;
      border-radius: 50%;
    }
    & .middle {
      padding-top: .9rem;
      flex: 1;
      & h3 {
        font-size: 1.1rem;
        font-weight: 400;
        color: #333333;
      }
      & p {
        font-size: .85rem;
        margin-top: .7rem;
        color: #989898;
      }
    }
    & .right {
      color: #333333;
      height: 3rem;
      padding-top: .9rem;
      padding-right: .8rem;
    }
  }
}
</style>



